body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
	box-sizing: border-box;
	font-family: 'Poppins', sans-serif;
	transition: all 0.2s;
}

.text-primary{
	color: #0050ff !important;
}

a{
	cursor: pointer;
}

h3{
	font-size: 1.17rem;
}

#root{
	overflow: hidden;
}

::-webkit-scrollbar {
	width: 8px;
}
  
/* Track */
::-webkit-scrollbar-track {
	border-radius: 10px;
}
   
/* Handle */
::-webkit-scrollbar-thumb {
	background: rgb(128, 128, 128); 
	border-radius: 10px;
}
  
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
	background: rgb(100, 100, 100); 
}

/* Header */
.flex-container{
  	display: flex;
	height: -webkit-max-content;
	height: max-content;
	width: 100%;
	position: fixed;
	top: 0px;
	z-index: 5;
	/* box-shadow: 0px 10px 10px rgba(90, 90, 90, 0.2); */
}

.flex-col{
	flex: 50% 1;
	padding: 40px;
}

.float-left{
	float: left;
}

.bullet-container{
	position: absolute;
	-webkit-transform: translate(0, -50%);
	        transform: translate(0, -50%);
	top: 50%;
	left: 50px;
	width: 15px;
	margin-right: 10px;
}

.bullet{
	width: 15px;
	height: 15px;
	background-color: #0050ff;
}

.details-container{
	height: 100%;
	align-items: center;
	justify-content: center;
}

.details-container-inner{
	-webkit-transform: translate(0%, -50%);
	        transform: translate(0%, -50%);
    top: 50%;
    position: absolute;
	left: 70px;
}

.inner-container{
	align-items: center;
}

.basic-details-container{
	flex: 40% 1;
	background-color: white;
}

.navOnScroll{
	box-shadow: 0px 10px 10px rgba(90, 90, 90, 0.2);
	position: fixed;
	top: 0px;
}

.nav-container{
	flex: 60% 1;
	padding: 30px;
}

.nav-container ul {
	list-style-type: none;
	margin: 0;
	padding: 0;
	overflow: hidden;
	background-color: white;
	top: 0;
}
  
.nav-container li {
	float: right;
}
  
.nav-container li a {
	display: block;
	color: black;
	text-align: center;
	padding: 14px 16px;
	text-decoration: none;
}
  
.nav-container li a:hover {
	background-color: white;
	color: #0050ff;
}

.nav-container li a:active{
	background-color: white !important;
}

.activeLink{
	color: #0050ff !important;
}

.nav-toggle-btn{
	display: none;
}

.nav-toggle-btn span{
	display: block;
	width: 40px;
	height: 4px;
	transition: 0.5s;
	background-color: rgb(51, 51, 51);
	box-shadow: 0 2px 2px rgba(90, 90, 90, 0.2);
}

.nav-toggle-btn span:nth-child(1){
	-webkit-transform: translateY(-5px);
	        transform: translateY(-5px);
	transition: 0.5s;
}

.navActive{
	color: #0050ff !important;
}
.desktop-nav a, .tablet-nav a{
	transition: all 0.5s;
}

.nav-toggle-btn span:nth-child(4){
	-webkit-transform: translateY(5px);
	        transform: translateY(5px);
	transition: 0.5s;
}

.nav-toggle-btn span:nth-child(2){
	-webkit-transform: translateY(2px);
	        transform: translateY(2px);
	transition: 0.5s;
}

.nav-toggle-btn span:nth-child(3){
	-webkit-transform: translateY(-2px);
	        transform: translateY(-2px);
	transition: 0.5s;
}

.nav-toggle-move-down{
	/* transform: translateY(15px) !important; */
	opacity: 0;
	display: none;
}

.nav-toggle-move-up{
	/* transform: translateY(-15px) !important; */
	opacity: 0;
	display: none;
}

.nav-toggle-rorate-ac{
	-webkit-transform: translateY(2px) rotate(45deg) !important;
	        transform: translateY(2px) rotate(45deg) !important;
}

.nav-toggle-rotate-c{
	-webkit-transform: translateY(-2px) rotate(-45deg) !important;
	        transform: translateY(-2px) rotate(-45deg) !important;
}

.nav-container{
	background-color: white;
	/* transition: 0.5s; */
}

ul.desktop-nav{
	margin-left: 200px;
}

