.heading-cover-enter-animation, 
.education-details-cover-enter-animation{
	-webkit-animation: fade-in-right-dual 0.5s ease-out 0.5s 1 normal both;
	        animation: fade-in-right-dual 0.5s ease-out 0.5s 1 normal both;
}

.picture-connect-enter-animation{
	-webkit-animation: fade-in-emerge-bottom 0.5s ease-out 1.5s 1 normal both;
	        animation: fade-in-emerge-bottom 0.5s ease-out 1.5s 1 normal both;
	position: relative;
	z-index: -1;
	margin-left: auto;
    margin-right: auto;
}

.experience-details-cover-enter-animation, 
.skills-details-cover-enter-animation, 
.heading-cover-enter-animation-dual, 
.contact-cover-enter-animation{
	-webkit-animation: fade-in-left 0.5s ease-out 0s 1 normal both;
	        animation: fade-in-left 0.5s ease-out 0s 1 normal both;
	/* position: relative; */
	z-index: 1;
}

.about-desc-enter-animation{
	-webkit-animation: fade-in-emerge-right 0.5s ease-out 1s 1 normal both;
	        animation: fade-in-emerge-right 0.5s ease-out 1s 1 normal both;
	position: relative;
	z-index: -1;
}
.project-details-enter-animation{
